'use client';

import { useState, useRef, useEffect } from 'react';
import { Card, CardHeader, CardTitle } from '@/components/ui/Card';
import { Button } from '@/components/ui/Button';
import { Badge } from '@/components/ui/Badge';
import { Input } from '@/components/ui/Input';
import { cn } from '@/lib/cn';
import {
  Send,
  Bot,
  User,
  Settings,
  Wrench,
  Zap,
  RotateCcw,
} from 'lucide-react';

interface Message {
  id: string;
  role: 'user' | 'assistant' | 'tool';
  content: string;
  toolName?: string;
  tokens?: number;
  timestamp: Date;
}

const MODELS = [
  { id: 'gpt-4o', name: 'GPT-4o', price: '$2.50 / $10.00' },
  { id: 'gpt-4o-mini', name: 'GPT-4o Mini', price: '$0.15 / $0.60' },
  { id: 'claude-3-5-sonnet', name: 'Claude 3.5 Sonnet', price: '$3.00 / $15.00' },
  { id: 'gemini-2.0-flash', name: 'Gemini 2.0 Flash', price: '$0.10 / $0.40' },
];

const TOOLS = [
  { id: 'web_search', name: 'Web Search', enabled: true },
  { id: 'read_url', name: 'Read URL', enabled: true },
  { id: 'calculator', name: 'Calculator', enabled: false },
  { id: 'code_exec', name: 'Code Execution', enabled: false },
];

export function PlaygroundChat() {
  const [messages, setMessages] = useState<Message[]>([]);
  const [input, setInput] = useState('');
  const [isLoading, setIsLoading] = useState(false);
  const [selectedModel, setSelectedModel] = useState(MODELS[0]);
  const [tools, setTools] = useState(TOOLS);
  const [temperature, setTemperature] = useState(0.7);
  const messagesEndRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    messagesEndRef.current?.scrollIntoView({ behavior: 'smooth' });
  }, [messages]);

  const handleSend = async () => {
    if (!input.trim() || isLoading) return;

    const userMessage: Message = {
      id: `msg_${Date.now()}`,
      role: 'user',
      content: input,
      timestamp: new Date(),
    };

    setMessages((prev) => [...prev, userMessage]);
    setInput('');
    setIsLoading(true);

    // Simulate response
    setTimeout(() => {
      const assistantMessage: Message = {
        id: `msg_${Date.now()}`,
        role: 'assistant',
        content: `This is a simulated response to: "${input}"\n\nIn a real implementation, this would call the Cogitator runtime and stream the response back.`,
        tokens: Math.floor(Math.random() * 500) + 100,
        timestamp: new Date(),
      };
      setMessages((prev) => [...prev, assistantMessage]);
      setIsLoading(false);
    }, 1500);
  };

  const toggleTool = (toolId: string) => {
    setTools((prev) =>
      prev.map((t) =>
        t.id === toolId ? { ...t, enabled: !t.enabled } : t
      )
    );
  };

  const clearChat = () => {
    setMessages([]);
  };

  return (
    <div className="h-full flex">
      {/* Chat Area */}
      <div className="flex-1 flex flex-col">
        {/* Messages */}
        <div className="flex-1 overflow-auto p-6">
          <div className="max-w-3xl mx-auto space-y-6">
            {messages.length === 0 ? (
              <div className="text-center py-20">
                <Bot className="w-12 h-12 text-accent mx-auto mb-4" />
                <h2 className="text-xl font-semibold text-text-primary mb-2">
                  Agent Playground
                </h2>
                <p className="text-text-secondary max-w-md mx-auto">
                  Test your agents in real-time. Select a model, enable tools,
                  and start chatting.
                </p>
              </div>
            ) : (
              messages.map((message) => (
                <div
                  key={message.id}
                  className={cn(
                    'flex gap-4 animate-fade-in',
                    message.role === 'user' && 'flex-row-reverse'
                  )}
                >
                  <div
                    className={cn(
                      'w-8 h-8 rounded-lg flex items-center justify-center flex-shrink-0',
                      message.role === 'user'
                        ? 'bg-chart-2/10'
                        : message.role === 'tool'
                        ? 'bg-chart-4/10'
                        : 'bg-accent/10'
                    )}
                  >
                    {message.role === 'user' ? (
                      <User className="w-4 h-4 text-chart-2" />
                    ) : message.role === 'tool' ? (
                      <Wrench className="w-4 h-4 text-chart-4" />
                    ) : (
                      <Bot className="w-4 h-4 text-accent" />
                    )}
                  </div>
                  <div
                    className={cn(
                      'flex-1 max-w-[80%]',
                      message.role === 'user' && 'text-right'
                    )}
                  >
                    {message.toolName && (
                      <Badge variant="outline" size="sm" className="mb-2">
                        {message.toolName}
                      </Badge>
                    )}
                    <div
                      className={cn(
                        'rounded-xl p-4',
                        message.role === 'user'
                          ? 'bg-chart-2/10 text-text-primary inline-block'
                          : 'bg-bg-secondary border border-border-subtle'
                      )}
                    >
                      <p className="text-sm whitespace-pre-wrap">
                        {message.content}
                      </p>
                    </div>
                    {message.tokens && (
                      <div className="flex items-center gap-1 mt-1 text-xs text-text-muted">
                        <Zap className="w-3 h-3" />
                        {message.tokens} tokens
                      </div>
                    )}
                  </div>
                </div>
              ))
            )}
            {isLoading && (
              <div className="flex gap-4 animate-fade-in">
                <div className="w-8 h-8 rounded-lg bg-accent/10 flex items-center justify-center">
                  <Bot className="w-4 h-4 text-accent" />
                </div>
                <div className="bg-bg-secondary border border-border-subtle rounded-xl p-4">
                  <div className="flex gap-1">
                    <span className="w-2 h-2 rounded-full bg-accent animate-bounce" />
                    <span
                      className="w-2 h-2 rounded-full bg-accent animate-bounce"
                      style={{ animationDelay: '150ms' }}
                    />
                    <span
                      className="w-2 h-2 rounded-full bg-accent animate-bounce"
                      style={{ animationDelay: '300ms' }}
                    />
                  </div>
                </div>
              </div>
            )}
            <div ref={messagesEndRef} />
          </div>
        </div>

        {/* Input */}
        <div className="border-t border-border-subtle p-4 bg-bg-secondary">
          <div className="max-w-3xl mx-auto flex gap-3">
            <Input
              placeholder="Type a message..."
              value={input}
              onChange={(e) => setInput(e.target.value)}
              onKeyDown={(e) => e.key === 'Enter' && !e.shiftKey && handleSend()}
              className="flex-1"
            />
            <Button
              variant="primary"
              onClick={handleSend}
              disabled={!input.trim() || isLoading}
            >
              <Send className="w-4 h-4" />
            </Button>
          </div>
        </div>
      </div>

      {/* Sidebar */}
      <div className="w-80 border-l border-border-subtle bg-bg-secondary p-4 space-y-6 overflow-auto">
        {/* Model Selection */}
        <Card>
          <CardHeader>
            <CardTitle className="flex items-center gap-2">
              <Settings className="w-4 h-4" />
              Model
            </CardTitle>
          </CardHeader>
          <div className="space-y-2">
            {MODELS.map((model) => (
              <button
                key={model.id}
                onClick={() => setSelectedModel(model)}
                className={cn(
                  'w-full p-3 rounded-lg text-left transition-colors',
                  selectedModel.id === model.id
                    ? 'bg-accent/10 border border-accent'
                    : 'bg-bg-elevated hover:bg-bg-hover border border-transparent'
                )}
              >
                <p className="text-sm font-medium text-text-primary">
                  {model.name}
                </p>
                <p className="text-xs text-text-tertiary">{model.price}</p>
              </button>
            ))}
          </div>
        </Card>

        {/* Temperature */}
        <Card>
          <CardHeader>
            <CardTitle>Temperature</CardTitle>
            <span className="text-sm text-accent">{temperature}</span>
          </CardHeader>
          <input
            type="range"
            min="0"
            max="2"
            step="0.1"
            value={temperature}
            onChange={(e) => setTemperature(parseFloat(e.target.value))}
            className="w-full accent-accent"
          />
          <div className="flex justify-between text-xs text-text-muted mt-1">
            <span>Precise</span>
            <span>Creative</span>
          </div>
        </Card>

        {/* Tools */}
        <Card>
          <CardHeader>
            <CardTitle className="flex items-center gap-2">
              <Wrench className="w-4 h-4" />
              Tools
            </CardTitle>
          </CardHeader>
          <div className="space-y-2">
            {tools.map((tool) => (
              <label
                key={tool.id}
                className="flex items-center justify-between p-2 rounded-lg hover:bg-bg-hover cursor-pointer"
              >
                <span className="text-sm text-text-primary">{tool.name}</span>
                <input
                  type="checkbox"
                  checked={tool.enabled}
                  onChange={() => toggleTool(tool.id)}
                  className="w-4 h-4 accent-accent"
                />
              </label>
            ))}
          </div>
        </Card>

        {/* Actions */}
        <Button
          variant="outline"
          className="w-full gap-2"
          onClick={clearChat}
        >
          <RotateCcw className="w-4 h-4" />
          Clear Chat
        </Button>
      </div>
    </div>
  );
}

